.timeline::before {
    content: '';
    position: absolute;
    top: 50%;
    left:0;
    right:0px;
    height:12px; 
    background:#111111; 
    background:var(--heading-text-color);
    margin-top:-6px;
}
.timeline .timeline-item{
	text-align:center;
	padding:0px 15px;
	position:relative;   
}
.timeline .timeline-item:before{
	background:#111111;
	background:var(--heading-text-color);
	height:32px;
	width:32px;
	content:"";
	position:absolute;
	top:50%;
	left:50%; 
	-webkit-transform:translate(-50%, -50%); 
	        transform:translate(-50%, -50%);
	border-radius:50px;
	border:4px solid #ffffff;
	border:4px solid var(--white-color);      
}
.timeline .step-number{
	color:#111111;
	color:var(--heading-text-color);
	font-family: 'Alegreya', serif;
	font-style:italic;
	font-size:98px;
	font-weight:bold;
	line-height:0.8;
	height:98px; 
}
.timeline .step-number span{
	display:block; 
}
.timeline .step-number{
	position:absolute;
	text-align:center;
	width:100%;
	top:30px;
}
.timeline .timeline-wrapper{
	padding-top:100px;
	position:relative; 
}
.timeline .timeline-item .content{
	padding:80px 0 0 0; 
}
.timeline .timeline-item .content p{
	margin-bottom:0; 
}
.timeline .timeline-item h4{
	font-size:24px;
	margin-bottom:10px; 
}
.timeline .timeline-item:nth-child(even) .timeline-wrapper{
	padding-top:0px;
	padding-bottom:100px; 
}
.timeline .timeline-item:nth-child(even) .content{
	padding:0 0 80px 0;
}
.timeline .timeline-item:nth-child(even) .step-number{
	top:auto;
	bottom:30px;
}
